import React from "react";

function Project(props) {
    return (
        <a href={props.siteLink}className="project">
                <div className="projectImages">
                    <img src={props.desktopImage} alt="project"/>
                    <img src={props.laptopImage} alt="project"/>
                    <img src={props.phoneImage} alt="project"/>
                </div>
                <div className="projectTexts">
                    <h2>{props.name}</h2>
                    <p>{props.description}</p>
                </div>
                <div className="links">
                    <a href={props.siteLink} className="site">Open Site</a>
                    <a href={props.codeLink} className="code">View Code</a>
                </div>
            </a>
    )
}

export default Project;