import React from "react";
import Logo from "../images/logo.png";
import { Link } from "react-router-dom";
import Resume from "../images/myResume.pdf";
import DarkMode from "@material-ui/icons/Brightness4";
import LightMode from "@material-ui/icons/Brightness5";

function Nav(props) {
    return(
        <nav>
            <div id="hamburger" onClick={props.clickHamburger}>
                <span></span>
                <span></span>
                <span></span>
            </div>

            <Link to="/" className="logo">
                <img srcSet={Logo} alt="Logo" />
                <h2>Cyril</h2>
            </Link>
            <div className="links">
                <Link to="/">Home</Link>
                <Link to="/projects">Projects</Link>
                <Link to="/about">About</Link>
                <Link to="/contacts">Contacts</Link>
                <a href={Resume}>
                    Resume
                </a>
            </div>

            {props.darkMode? <LightMode onClick={props.clickDarkMode} /> : <DarkMode onClick={props.clickDarkMode} />}

        </nav>
    )
}

export default Nav;