import React from "react";
import TechnicalSkills from "../images/technical_skills.PNG";
import IntellectualSkills from "../images/intellectual_skills.PNG";
import CheckBoxIcon from "@material-ui/icons/CheckBoxOutlined";

function About() {
    return(
        <div className="about">
            <div className="top">
                <p>I'm Cyril Asogwa. I've been on the journey of Full Stack Web Development since December 2021, successfully going through a total of 72 hours of lessons on front end, back end, frameworks and basic design modules.</p>
                <p>
                I'm a passionate and dedicated web developer that designs user interactions on websites, develop servers and databases for website functionality and coding mobile platforms.
                </p>
                <p>To me, Web Development has become more than work, it's become a hobby and every single day that has gone by has never been complete if I haven't spent at least an hour a day working on a project.</p>
                <p>I've had some experience working in remote locations (working from home) in platforms like Zuri team | Ingressive for good and in HNG as an intern and based on their reports, I will be able to work in any institution, organisation or company I find myself in and yield positive results.</p>
            </div>
            
            <div className="bottom">
                <div className="skills">
                    <img src={TechnicalSkills} alt="technical"/>
                    <h2>Technical Skills</h2>
                    <div className="skillsLists">
                        <p>
                            <CheckBoxIcon />
                            HTML: 5
                        </p>
                        <p>
                            <CheckBoxIcon />
                            CSS
                        </p>
                        <p>
                            <CheckBoxIcon />
                            SASS
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Bootstrap
                        </p>
                        <p>
                            <CheckBoxIcon />
                            JavaScript
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Jquery
                        </p>
                        <p>
                            <CheckBoxIcon />
                            React.js
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Git Cli
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Node.js
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Embedded JavaScript Templating (EJS)
                        </p>
                        <p>
                            <CheckBoxIcon />
                            MongoDb
                        </p>
                        <p>
                            <CheckBoxIcon />
                            User Authentication
                        </p>
                    </div>
                </div>
                <div className="skills">
                    <img src={IntellectualSkills} alt="intellectual"/>
                    <h2>Intellectual Skills</h2>
                    <div className="skillsLists">
                        <p>
                            <CheckBoxIcon />
                            Positive Attitude
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Good Communication Skills
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Good Time Management
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Quick Learning Ability
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Good Team Player
                        </p>
                        <p>
                            <CheckBoxIcon />
                            Big-Picture Focus
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About;