import React,{ useState, useEffect } from "react";
import {BrowserRouter as Router} from "react-router-dom";
import { Route, Switch } from "react-router-dom/cjs/react-router-dom.min";
import Nav from './components/Nav';
import MobileNav from "./components/projectsComponents/MobileNav";
import Home from "./components/Home";
import Book from "./components/Book";
import Connect from "./components/Connect";
import Projects from "./components/Projects";
import About from "./components/About";
import Contacts from "./components/Contacts";
import NotFound from './components/NotFound';
import Footer from './components/Footer';
import './scss/styles.css';

function App() {
  const [hamburger, setHamburger] = useState(false);
  const [darkMode, setdarkMode] = useState(false);

  useEffect(() => {
    const prefersDarkMode = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;

    setdarkMode(prefersDarkMode);

    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    const handleChange = () => setdarkMode(mediaQuery.matches);

    mediaQuery.addEventListener('change', handleChange);
    return () => mediaQuery.removeEventListener('change', handleChange);
  }, []);

  function clickHamburger() {
    setHamburger((prevValue) => {
      return !prevValue;
    })
  }
  const clickDarkMode = ()=> {
    setdarkMode((prevValue) => {
      return !prevValue
    });
  }
  return (
    <Router>
      <div className={!darkMode? "App" : "App DarkMode"}>
      <header className={!hamburger? "App-header" : "App-header burger"}>
        <Nav
         clickHamburger={clickHamburger}
         darkMode={darkMode}
         clickDarkMode={clickDarkMode}
         />
         {hamburger && <MobileNav clickHamburger={clickHamburger} />}

         <Switch>
           <Route path='/' exact>
             <Home />
           </Route>

           <Route path='/book' exact>
             <Book />
           </Route>

           <Route path='/connect' exact>
             <Connect />
           </Route>

           <Route path='/projects' exact>
             <Projects />
           </Route>

           <Route path='/about' exact>
             <About />
           </Route>

           <Route path='/contacts' exact>
             <Contacts />
           </Route>

           <Route path='*' exact>
             <NotFound />
           </Route>
         </Switch>

        <Footer />
      </header>
    </div>
    </Router>
  );
}

export default App;
